<template>
  <el-popover
    effect="light"
    popper-class="tooltip-table"
    trigger="click"
    placement="left-end"
    v-model="isVisible"
    :visible-arrow="true">
    <el-row>
      <!-- 流程名称 -->
      <el-col :span="24">
        <div class="flow-top">
          <div class="title">{{ data.name }}</div>
          <i @click="isVisible = !isVisible" style="position: absolute; top: 10px; right: 10px;" class="iconfont icon-guanbipre"></i>
          <table>
            <tr class="flowchart">
              <th class="name">流程ID</th>
              <td class="des">
                <span>{{ data.id }}</span>
              </td>
            </tr>
            <tr class="flowchart">
              <th colspan="1" class="name">流程描述</th>
              <td colspan="3" class="des">
                <div  v-html="processInfo.processDescription"></div>
              </td>
            </tr>
          </table>
        </div>
      </el-col>
    </el-row>
    <!-- 流程图  -->
    <el-row>
      <el-col :span="24" >
        <div class="flowchart-img" :style="{'max-height': processHeight+'px'}">
          <div class="subtitle" v-for="(item,index) in flowChart" :key="index" v-if="item.activityRemarkList.length > 0">
             <i class="line"></i>
            <span class="processTitle">{{item.processTemplateName}}</span>
            <div class="">
              <el-steps align-center :active="item.activeIndex">
                <div class="step-linerow" v-for="(activityRemark,index) in item.activityRemarkList" :key="index">
                  <el-step v-for="step in activityRemark" :key="step.activityID"
                           style="word-break: break-all;"
                   :title="step.activityName.length>27?`${step.activityName.substr(0,25)}...`:step.activityName"></el-step>
                </div>
              </el-steps>
            </div>
          </div>
        </div>
      </el-col>
    </el-row>
    <!-- 显示流程名 -->
    <i slot="reference" @click.stop="showPopver" style="color: #AAB4BC; font-size: 12px; margin-left: 10px;" class="iconfont icon-fangdanor"></i>
  </el-popover>
</template>
<script>
 import util from '../../js/util'
export default {
  name: "popover_flowsheet",
  props: {
    //数据
    data: {
      type: Object,
      required: true
    }
  },
  created() {
    // console.log(this.data);
  },
  data() {
    function setHeight(){
      let fullHeight = document.documentElement.clientHeight;
      if(fullHeight<=650){
        return  fullHeight+60
      }else{
        return fullHeight
      }
    }
    return {
      processHeight: setHeight() - 400, // 流程外层盒子高度
      processInfo:{
        processDescriptions:''
      },//流程 数据
      flowChart:[], // 流程图 数据
      isVisible: false
    };
  },
  methods: {
    /**
     * 弹窗显示触发
     */
    showPopver(){
      util.burialPoint('发起流程展开描述');
      var that = this;
      that.isVisible = false;
      that.$axios.post("/api/processCategoryImg", { processId: that.data.processId}).then(res => {
        // itmes 数组中 每个元素是一个流程图
          if(res.data.result.processInfo){
            this.flowChart = res.data.result.itmes;
            let index =0
            for(let it of this.flowChart){
              it.activityRemarkList.unshift({'activityID':`start_${index}`,'activityName':`开始`})
              it.activeIndex = it.activityRemarkList.length
              it.activityRemarkList = this.split(it.activityRemarkList)
              index++
            }
          }
        // processInfo 流程 数据
        if(res.data.result.processInfo){
          this.processInfo = res.data.result.processInfo;
        }

      }).catch(err => {
          that.$message.error(`获取流程图数据失败!ERROR MESSAGE:${err}`);
      });
    },
     /**
     * 点击 进入老系统
     */
    // openOldIframe(){
    //   let that = this
    //   if (that.data.haspermission == "1" && that.data.isOnlyBiz=='0') {
    //      //判断是否是IE
    //     if (util.browserIsIE()) {
    //       let routeData = that.$router.resolve({
    //         path: "/oldIframeDialog",
    //         query: { url: that.data.viewUrl }
    //       });
    //       window.open("index.html?"+routeData.href, "_blank");
    //     } else {
    //       window.open(that.data.viewUrl);
    //     }
    //   }
    // },
    //处理 list数据 转二维数组
    split(array) {
      let chunks = [];
      let count = Math.ceil(array.length / 5);
      while (count > 0) {
        chunks.push(array.slice((count - 1) * 5, count * 5));
        count--;
      }
      chunks = chunks.reverse();
      return chunks;
    }
  }
};
</script>

<style lang="scss" scoped>
  a,
  a.isaUnderline,a.isaUnderline{
    display: block;
    width: 260px;
    /*overflow: hidden;*/
    /*text-overflow: ellipsis;*/
    /*white-space: nowrap;*/
    text-decoration: none;
  }

.flow-top{
  white-space: nowrap;
  padding:30px 20px 20px;
  background-color:#fafbfb;
}

.tooltip-table {
  width: 500px;
  padding: 20px;
  .title {
    text-align: center;
    font-size:20px;
    color:#4d555d;
    line-height:44px;
  }

  table {
    width: 100%;
    text-align: left;
    color: #626262;
    border-collapse: separate;
    border-spacing: 0;

    .flowchart {
      width: 500px;
      vertical-align: top;
      .name {
        font-weight:100;
        text-align: right;
        padding: 10px;
        width: 60px;
        height: 100%;
        font-size:13px;
        color:#4d555d;
      }
      .des {
        // width: 120px;
        padding: 10px 0 10px 10px;
        line-height: 20px;
        font-size:13px;
        color:#a6b0b9;
        word-break: break-all;
        & div{
          max-height: 100px;
          overflow-y:auto;
          padding-right: 20px;
          // border:1px solid red;
        }
      }
    }
  }

    .subtitle{
     font-size:13px;
     color:#4d555d;
     padding-bottom:15px;
      span.processTitle {
        font-size: 16px;
      }
    .line{
      display:inline-block;
      width: 3px;
      height:16px;
      line-height:20px;
      background-color:#91cdf9;
      margin:0 5px -2px 20px;
    }
  }

  & .flowchart-img{
    // height: 230px;
    overflow-y:auto;
    padding: 20px 50px 20px 45px;
  }
}

</style>



