<template>
  <div class="initProcess loadingtext"
       v-loading="loading"
       element-loading-text="加载中..."
       element-loading-background="rgba(255, 255, 255, 0.8)">
    <el-container>
      <!-- 发起流程页面头部 -->
      <el-header>
        <div class="top-header">
          <el-row type="flex"  justify="start" >
            <span class="top-breadCrumb">发起流程</span>
          </el-row>
        </div>
      </el-header>
      <div class="searchPro">
        <el-row type="flex">
          <el-col :span="16"></el-col>
          <el-col :span="8" class="searchProflex">
            <el-checkbox v-model="checkValue" @change="changeCheckValue">显示全部流程</el-checkbox>
            <el-input v-model="searchProcessCode" placeholder="请输入内容" @keyup.enter.native="searchProcess()"></el-input>
            <el-button type="primary" plain @click="searchProcess">查询</el-button>
          </el-col>
        </el-row>
      </div>
      <!-- 发起流程页面内容数据 -->
      <el-main>
        <div class="mainContaint">
          <el-row :gutter="24" style="margin: 0 !important;">
             <!-- 流程分类 Category tree-->
            <el-col style="padding: 0">
              <div class="middle-content">
                <div class="process-map-content">
                  <!-- 第一级菜单-->
                   <div class="mapRoot">
                      <ul class="mapRoot-text-ul">
                          <li v-for="(item,index) in mapData.FirstLevelMap"
                                     :class="activeTab.RootProcess == index ? 'activeProcess': '' "
                                     ref="processdom"
                                     @click="rootProcessTab(item,index)"
                                     :key="item.ID"
                                     class="mapRoot-text">
                                     {{item.ProcessName}}</li>
                      </ul>
                   </div>
                  <!-- 第二级菜单-->
                  <div class="mapFrist">
                    <ul class="mapRoot-text-ul">
                      <li v-for="(item,index) in mapData.SecondLevelMap"
                          :class="activeTab.SecodeProcess == index ? 'activeProcess': '' "
                          :key="item.ID"
                          @click="secodeProcessTab(item,index)"
                          class="mapRoot-text">
                        {{item.ProcessName}}</li>
                    </ul>
                  </div>
                  <!-- 第三级菜单-->
                  <div class="mapProcess" :style="{'height':mapProcessHeight+'px'}" style="padding-top:10px;border-box:box-sizing;">
                    <div class="mapRoot-text-ul borB"  v-for="item in mapData.OtherLevelMap" :class="item.SType==='Process'?'progig':'clearfix'">
                        <div class="fz16 fif">
                          <!--第二级流程类别-->
                          <div v-if="item.SType==='Category'" class="clearfix">
                            <p class="fz13 font-b">[{{item.ProcessName}}]</p>
                            <div v-for="(i,index) in item.Children" :key="index"  :class="i.SType==='Process'?'fif-process':'fif-Category'" :span="6">
                              <!--判断是否是流程-->
                              <div v-if="i.SType==='Process'">
                                <div>
                                  <i class="icon iconfont" :class="i.IsFavorite?'icon-full-collect':'icon-collect'"
                                     @click.stop="Collectchange(i)" style="font-size:14px;vertical-align: middle;" v-if="i.StartUrl"></i>
                                  <el-tooltip
                                    effect="dark"
                                    :content="i.ProcessName"
                                    placement="right"
                                    :key="i.ID" v-if="i.StartUrl">
                                    <a class="fif-tag-pro" @click.stop="start(i)">{{i.ProcessName}}</a>
                                  </el-tooltip>
                                  <span class="noUrl" v-else>{{ i.ProcessName }}</span>
                                </div>
                              </div>
                              <!--判断是否是流程类别-->
                              <div v-if="i.SType==='Category'" class=" fif-tag-content fif-tag-title" >
                                <div>
                                  <p style="width: 100%">[{{i.ProcessName}}]</p>
                                  <div v-for="j in i.Children"  class="fif-g" v-if="i.Children">
                                    <i class="icon iconfont" :class="j.IsFavorite?'icon-full-collect':'icon-collect'"
                                       @click.stop="Collectchange(j)" style="font-size:14px;vertical-align: middle;" v-if="j.StartUrl"></i>
                                    <el-tooltip
                                      effect="dark"
                                      :content="j.ProcessName"
                                      placement="right"
                                      :key="i.ID" v-if="j.StartUrl">
                                      <a class="fif-tag-pro">{{j.ProcessName}}</a>
                                    </el-tooltip>
                                    <span v-if="j.SType==='Category' && !j.StartUrl">[{{j.ProcessName}}]</span>
                                    <span v-if="j.SType==='Process' && !j.StartUrl" class="noUrl">{{j.ProcessName}}</span>
                                  </div>
                                </div>
                              </div>
                            </div>
                          </div>
                          <!--第二级直接挂流程-->
                          <div v-else class="profif" style="display: flex;justify-content: flex-start">
                            <div>
                              <i class="icon iconfont" :class="item.IsFavorite?'icon-full-collect':'icon-collect'"
                                 @click.stop="Collectchange(item)" style="font-size:14px;vertical-align: middle;" v-if="item.StartUrl"></i>
                              <el-tooltip
                                effect="dark"
                                :content="item.ProcessName"
                                placement="right"
                                :key="item.ID" v-if="item.StartUrl">
                                <a class="fif-tag-pro" @click.stop="start(item)">{{item.ProcessName}}</a>
                              </el-tooltip>
                              <span class="noUrl" v-else>{{ item.ProcessName }}</span>
                            </div>
                          </div>
                         </div>
                    </div>
                  </div>
                </div>
              </div>
            </el-col>
          </el-row>
        </div>
      </el-main>
    </el-container>
  </div>
</template>

<script>
import util from "../../js/util";
import popover from "../../plugins/popover-flowsheet/index";
import { mapGetters } from 'vuex'

export default {
  name: "initProcess",
  data() {
    function setHeight(){
      let fullHeight = document.documentElement.clientHeight;
          return fullHeight
    }
    return {
      mapProcessHeight:setHeight() - 250, // 流程外层盒子高度
      loading:false,
      activeTab:{
        RootProcess: 0, // 0为默认选择第一个，-1为不选择,
        SecodeProcess:0
      },
      styleobj:{
        'background-color': '#941C25'
      },
      searchProcessCode:'',
      mapData:{},//流程地图数据,
      checkValue: false,
      categoryID: '',
      categoryID2: ''
    }
  },
  components: {
    popover,
  },
  created() {

  },
  mounted() {
    let _this = this;
    _this.getMapProcessData();
  },
  methods: {
    //获取初始流程地图数据
    getMapProcessData(){
      let _this = this;
      _this.loading  = true;
      let data = {
        categoryID: this.categoryID,
        categoryID2: this.categoryID2,
        key: this.searchProcessCode,
        showAll: this.checkValue
      };
      _this.$axios({
        url:'/Portal/WorkflowMap/GetWorkflowMapByCategoryId',
        method:'post',
        data:data,
      }).then(res => {
        if( res.data.code===200){
          _this.mapData = res.data.data
        }
      }).catch(err=>{

      })
      _this.loading  = false;
    },
    //一级菜单
    rootProcessTab(process,index){
      let _this = this;
      _this.loading = true;
      this.activeTab.RootProcess = index;
      _this.activeTab.SecodeProcess = 0;
      this.categoryID = process.ID
      this.categoryID2 = ''
      let data = {
        categoryID: this.categoryID,
        categoryID2: this.categoryID2,
        key: this.searchProcessCode,
        showAll: this.checkValue
      };
      _this.$axios.post("/Portal/WorkflowMap/GetWorkflowMapByCategoryId",data).then(res => {
        if(res.data.code===200){
          _this.loading = false;
          _this.mapData = res.data.data;
          // _this.mapData.OtherLevelMap = res.data.data.OtherLevelMap;
        }
      }).catch(err=>{

      })
    },
    //二级菜单
    secodeProcessTab(process,index){
      let _this = this;
      _this.activeTab.SecodeProcess = index;
      _this.loading = true;
      this.categoryID2 = process.ID
      let data = {
        categoryID: this.categoryID,
        categoryID2: this.categoryID2,
        key: this.searchProcessCode,
        showAll: this.checkValue
      };
      _this.$axios.post("/Portal/WorkflowMap/GetWorkflowMapByCategoryId",data).then(res => {
        if(res.data.code===200){
          _this.loading = false;
           _this.mapData = res.data.data;
          // _this.mapData.OtherLevelMap = res.data.data.OtherLevelMap;
        }
      }).catch(err=>{

      })
    },
    //流程收藏
    Collectchange(val){
      var _this = this;
      if(!val.IsFavorite){
        let data = {
          processCode:val.ProcessCode
        };
        _this.$axios({
          url:'/Portal/WorkflowMap/AddWorkflowFavorite',
          method:'POST',
          data:data
        }).then(res=>{
          if(res.data.code===200){
            val.IsFavorite = !val.IsFavorite
          }
          else {
            _this.$message.success('取消收藏失败');
          }
        }).catch(err=>{
          _this.$message.error(`数据获取失败!ERROR MESSAGE:${err.message}`);
        })
      }
      else {
        let data = {
          processCode:val.ProcessCode
        };
        _this.$axios({
          url:'/Portal/WorkflowMap/DelWorkflowFavorite',
          method:'POST',
          data:data
        }).then(res=>{
          if(res.data.code===200){
            val.IsFavorite = !val.IsFavorite;
          }
          else {
            _this.$message.success('收藏失败');
          }
        }).catch(err=>{
          _this.$message.error(`数据获取失败!ERROR MESSAGE:${err.message}`);
        })
      }
    },
    //发起
    start(row){
      window.open(row.StartUrl,'_blank');
    },
    //查询
    searchProcess(){
      let _this = this
      _this.loading  = true;
      this.activeTab.RootProcess = 0;
      _this.activeTab.SecodeProcess = 0;
      this.categoryID = ''
      this.categoryID2 = ''
      let data = {
        categoryID: this.categoryID,
        categoryID2: this.categoryID2,
        key: this.searchProcessCode,
        showAll: this.checkValue
      };
      _this.$axios({
        url:'/Portal/WorkflowMap/GetWorkflowMapByCategoryId',
        method:'post',
        data:data,
      }).then(res => {
        if( res.data.code===200){
          _this.mapData = res.data.data
        }
      }).catch(err=>{

      })
      _this.loading  = false;
    },
    changeCheckValue() {
      this.searchProcessCode = ''
      this.searchProcess()
    }
  },
  // 计算属性
  computed: {
    bodyHeight() {
      return document.documentElement.clientHeight;
    },
//    ...mapGetters(['bodyHeight'])
  }
};
</script>

<style lang="scss" scoped>
.mainContaint{
  font-family: 微软雅黑;
  padding-left: 0;
  .noUrl {
    color: #A0A0A0;
  }
  .middle-content{
    .process-map-content {
      .mapRoot {
        .mapRoot-text-ul {
          display: flex;
          justify-content: flex-start;
          flex-wrap: wrap;
          height: 100%;
          /*line-height: 25px;*/

          .mapRoot-text {
            color: #000000;
            font-size: 15px;
            line-height: 30px;
            padding: 0 15px;
            cursor: pointer;
            font-weight: bold;
          }
        }
        .borB{

          /*border-bottom: 1px solid #EFE8E8;*/
        }
      }
      .mapFrist {
        margin-top: 20px;
        .mapRoot-text-ul {
          display: flex;
          justify-content: flex-start;
          flex-wrap: wrap;
          .mapRoot-text {
            color: #222222;
            font-size: 14px;
            line-height: 30px;
            padding: 0 15px;
            cursor: pointer;
            font-weight: bold;
          }
        }
        .borB{
          /*border-bottom: 1px solid #EFE8E8;*/
        }
      }
      .mapProcess{
        min-width: 1280px;
        /*overflow-y: auto;*/
        margin-bottom: 20px;
        .mapRoot-text-ul {
          display: flex;
          justify-content: flex-start;
          flex-wrap: wrap;
          .mapRoot-text {
            padding: 10px 15px 10px 0;
            color: #A6424A;
            display: inline-block;
            width: 200px;
            /* width: 230px; */
            overflow: hidden;
            white-space: nowrap;
            text-overflow: ellipsis;
            font-size: 14px;
            .process-item{
              padding: 10px 15px 5px 15px;
            }
          }
        }
        .borB{
          /*border-bottom: 1px solid #EFE8E8;*/
        }
      }
    }
  }
  .fz16{
    padding: 5px 15px 5px 0;
    font-size: 16px;
    color: #222222;
  }
  .fz13{
    padding: 5px 15px 5px 0;
    font-size: 13px;
    color: #8c8c8c;
  }
  .font-b{
    font-weight: bold;
  }
  .fif-tag{
    display: flex;
    justify-content: flex-start;
    align-items: center;
    .fif-tag-title{
      padding: 10px 15px 5px 15px;
      display: inline-block;
      font-size: 12px;
      width: 153px;
      color: #A0A0A0;
    }
    .fif-tag-content{
      display: block;
      color: #A6424A;
      width: 200px;
      overflow: hidden;
      white-space: nowrap;
      text-overflow: ellipsis;
      font-size: 12px;
      .fif-tag-pro{
        font-size: 14px;
        font-weight: normal;
      }
    }

  }
}
.activeProcess{
  background-color: #941C25;
  border-radius: 5px;
  -moz-border-radius: 5px;
  -webkit-border-radius: 5px;
  color: #ffffff!important;
  cursor: pointer;
  text-align: center;
  font-weight: normal;
}
.fif-process{
  font-size: 13px;
  color: #A6424A;
  float: left!important;
  padding: 10px 15px 5px 0px;
  width: 235px;
  height: 20px;
  a{
    width: 200px;
    display: inline-block;
    cursor: pointer;
    outline: none;
  }
  & a:hover{
    text-decoration: underline;
  }
}
.fif-Category{
    display: block;
    padding: 10px 15px 15px 0px;
    width: 100%;
    clear: both;
    a{
      width: 200px;
      cursor: pointer;
      outline: none;
    }
    p{
      display: inline-block;
      width: 230px;
      padding: 5px 0 5px 0;
      font-size: 13px;
      color:#A0A0A0;
    }
    .fif-g{
      padding: 10px 15px 5px 0px;
      width: 235px;
      font-size: 13px;
      color: #A6424A;
      display: inline-block;
    }
    .fif{
      width: 100%;
    }
  }
.profif{
  line-height: 15px;
  font-size: 13px;
  color: #A6424A;
  float: left!important;
  padding: 5px 0;
  a{
    width: 200px;
    display: inline-block;
    cursor: pointer;
    overflow: hidden;
    text-overflow: ellipsis;
    white-space:nowrap;
    outline: none;
  }
  & a:hover{
    text-decoration: underline;
  }
}
.searchPro{
  .el-input__inner{
    height: 30px!important;
    line-height: 30px!important;
  }
  height: 26px;
  line-height: 26px;
  text-align: right;
  width: 100%;
  padding: 10px 20px;
  .searchProflex{
    display: flex;
    justify-content: flex-end;
    margin-right: 40px;
    .el-input{
      margin-right: 15px;
      border-radius: 5px;
    }
    .el-button--primary.is-plain,.el-button{
      padding:0 17px;
      background: #66b1ff;
      border-color: #66b1ff;
      color: #fff;
      height: 40px;
      border-radius: 5px;
    }
  }
}
.progig{
  width: 250px;
  float: left;
  height: 38px;
}
  .clearfix{
    clear: both;
  }
</style>

<style lang="scss">
.searchProflex {
    .el-checkbox {
      line-height: 40px;
      margin-right: 15px;
      vertical-align: middle;
    }
    .el-checkbox__label {
      font-size: 14px !important;
      color: #A6424A !important;
    }
  }
  @media screen and (max-width: 1300px) {
    
    .mainContaint {
      .rightProcess {
        background: url(../../assets/process.png) no-repeat right center;
        // -webkit-background-size: 70% !important;
        // background-size: 70% !important;
      }
      //右侧背景图片控制
      .img-right{
        width: 99%;
        padding-left: 320px !important;
      }
      //右侧背景图片控制 -- 全屏
      .img-right-all{
          width: 100%;
          padding-right: 0;
          background-size: 100% !important;
      }
    }
  }
  .icon-full-collect{
    color: #941C25;
  }
  
</style>



